}
/* necessary to give position: relative to parent. */
input[type='text'],
input[type='email'],
input[type='password'] {
  font: 1rem/1.5rem 'Lato', Arial, sans-serif;
  color: #fff;
  width: 100%;
  box-sizing: border-box;
  letter-spacing: 1px;
}

.style-input {
  border: 0;
  padding: 4px 0;
  border-bottom: 1px solid #ccc;
  background-color: transparent;
  outline: none;
}

.style-input ~ .focus-border {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 2px;
  background-color: #6de0ca;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}
.style-input:focus ~ .focus-border,
.has-content.style-input ~ .focus-border {
  width: 100%;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.style-input ~ label {
  position: absolute;
  left: 0;
  width: 100%;
  top: 9px;
  color: #6de0ca;
  -webkit-transition: 0.33s;
  transition: 0.33s;
  z-index: 0;
  letter-spacing: 0.5px;
}

.style-input:focus ~ label,
.has-content.style-input ~ label {
  top: -16px;
  font-size: 12px;
  color: #6de0ca;
  -webkit-transition: 0.33s;
  transition: 0.33s;
}

