.main-container {
  display: grid;
}

.edit {
  background-color: #36485f;
  color: #fff;
  padding: 0.5rem;
  border-radius: 4px;
  cursor: pointer;
}
.edit:hover {
  background-color: rosybrown;
}

.delete {
  background-color: #36485f;
  color: #fff;
  padding: 0.5rem;
  border-radius: 4px;
  cursor: pointer;
  border: 2px solid #36485f;
}

.delete:hover {
  background-color: cadetblue;
  border: 2px solid cadetblue;
}

.btn-div {
  margin-top: 1rem;
  display: flex;
  justify-content: space-between;
}

.quizset {
  font-size: 1.5rem;
  margin-bottom: 1rem;
}
