@import 'reset.css';
@import 'auth.css';
@import 'fourofour.css';
@import 'showquiz.css';
@import 'createquiz.css';
@import 'header.css';
@import 'setting.css';
@import 'listquizcreated.css';
@import url('https://fonts.googleapis.com/css?family=Lato&display=swap');

*,
*::after,
*::before {
  box-sizing: border-box;
  font-size: 16px;
  font-family: 'Lato', sans-serif;
}

body {
  background-color: #f3f3f3;
}

a {
  text-decoration: none;
  color: #000;
}

label {
  color: #fff;
}

.wrapper {
  margin-right: auto;
  margin-left: auto;
  max-width: 1400px;
  padding-right: 10px;
  padding-left: 10px;
}

.btn {
  width: auto;
  min-width: auto;
  background-color: #6de0ca;
  border-radius: 0.3rem;
  box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.2);
  cursor: pointer;
  align-self: center;
  transition: all 400ms ease 0s;
  align-content: center;
  padding: 0.75rem;
  border: 2px solid #6de0ca;
  color: #fcfcfc;
  font-weight: 500;
  font-size: 1.25rem;
  margin-top: 1rem;
  display: block;
}

.btn:hover {
  box-shadow: 0 3px 8px rgba(117, 117, 117, 0.5);
  user-select: none;
  background-color: #31dbbc;
  color: #fcfcfc;
}

.col-3 {
  float: left;
  width: 27.33%;
  margin: 40px 0;
  position: relative;
}
/* necessary to give position: relative to parent. */
input[type='text'],
input[type='email'],
input[type='password'] {
  font: 1rem/1.5rem 'Lato', Arial, sans-serif;
  color: #fff;
  width: 100%;
  box-sizing: border-box;
  letter-spacing: 1px;
}

.style-input {
  border: 0;
  padding: 4px 0;
  border-bottom: 1px solid #ccc;
  background-color: transparent;
  outline: none;
}

.style-input ~ .focus-border {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 2px;
  background-color: #6de0ca;
  transition: 0.4s;
}
.style-input:focus ~ .focus-border,
.has-content.style-input ~ .focus-border {
  width: 100%;
  transition: 0.4s;
}

.style-input ~ label {
  position: absolute;
  left: 0;
  width: 100%;
  top: 9px;
  color: #6de0ca;
  transition: 0.33s;
  z-index: 0;
  letter-spacing: 0.5px;
}

.style-input:focus ~ label,
.has-content.style-input ~ label {
  top: -16px;
  font-size: 12px;
  color: #6de0ca;
  transition: 0.33s;
}

/* 000 */
.create-btn {
  background-color: #004bfa;
  border: 2px solid #004bfa;
  border-radius: 4px;
  cursor: pointer;
  padding: 0.5rem;
  color: #fff;
}

.quiz-card {
}

.select {
  -webkit-appearance: none;
   -moz-appearance: none;
   appearance: none;
   padding: 0.5rem;
}

.table-head {
  font-size: 2.0rem;
  margin-bottom: 2rem;
  border-bottom: 2px orange;
  color: brown;
}

.table-container{
  display: flex;
  justify-content: space-evenly;
}

.table-data {
  font-size: 1.5rem;
  margin-bottom: 1rem;
}