header {
  background-color: #fff;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
}

.header-container {
  height: 3rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.setting-link > a {
  background-color: #36485f;
  color: white;
  padding: 0.5rem;
  border-radius: 4px;
}

.setting-link > a:hover {
  background-color: gray;
}

nav > a {
  padding: 0.5rem;
  margin-right: 0.5rem;
}

.lgt {
  border: 2px solid tomato;
  background-color: tomato;
  font-size: 1.25rem;
  border-radius: 4px;
  cursor: pointer;
  color: white;
}

.lgt:hover {
    background-color: rgba(255, 99, 71, 0.719);
}
