.auth-container > div {
  display: flex;
  justify-content: space-between;
  margin-top: 4rem;
}

.left-auth {
  width: 50%;
  height: 100%;
  display: block;
}

.right-auth {
  width: 50%;
  height: 100%;
  display: block;
  background-color: #35485f;
  padding: 1.5rem 1rem;
  border-radius: 7px;
}

.base-quiz-text-area {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.base-text {
  font-size: 2.5rem;
  font-weight: 200;
}

.quiz-text {
  font-size: 2.5rem;
  font-weight: 600;
}

.a-fun-place-text {
  font-size: 1.5rem;
  font-style: italic;
  margin: 1rem 0;
}

.vector-img {
  width: 35rem;
}

.user-admin-btn-area {
  display: inline-block;
}

.big-text {
  cursor: pointer;
  color: rgba(255, 255, 255, 0.5);
  font-size: 2.2rem;
  font-weight: 200;
  padding-bottom: 2px;
  border-bottom: 2px solid transparent;
  z-index: 2;
}

.input-container {
  display: grid;
  grid-template-rows: repeat(3, 1fr);
}

.user-admin-area {
  display: flex;
  align-self: center;
  justify-content: flex-end;
}

.user-admin-area-span {
  font-size: 1.5rem;
  color: rgba(255, 255, 255, 0.5);
  font-weight: 300;
  cursor: pointer;
  border-bottom: 2px solid transparent;
  letter-spacing: 2px;
}

.or {
  font-size: 1.5rem;
  margin: 0 1rem;
  cursor: default;
}

.active-underline {
  color: #fff;
  border-bottom: 2px solid #6de0ca;
}
