.tag {
  color: #fff;
  background-color: #36485f;
  padding: 0.25rem 0.75rem;
  display: block;
  border-radius: 4px;
  font-size: 1.25rem;
  margin-top: 1rem;
  cursor: pointer;
}

.tag-container {
  display: flex;
}

.card {
  border: 2px solid gray;
  border-radius: 6px;
  padding: 1rem;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
  background-color: #fff;
  margin-top: 1.5rem;
}

.card > h2 {
  font-size: 1rem;
  margin-top: 0.75rem;
  cursor: pointer;
}

.card > h2:active {
  color: white;
  background-color: black;
}

.card > h2:hover {
  color: blue;
  background-color: white;
}

.card > h1 {
  font-size: 1.5rem;
}

.main-container {
  display: flex;
  justify-content: space-evenly;
}

.card-container {
  display: grid;
  grid-template-rows: repeat(5, 1fr);
}

.top-container {
  display: flex;
  justify-content: flex-end;
}

.score {
  text-align: center;
  margin: 1rem 0;
  background-color: #004bfa;
  height: 1.75rem;
  color: #fff;
  padding: 0.5rem 0.75rem;
  border-radius: 4px;
}

.quizset-card {
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.quizset-text {
  font-size: 2rem;
}

.quizset-text-top {
  font-size: 2.5rem;
  margin-top: 2rem;
  display: block;
  color: #004bfa;
}

.quizzes-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50%;
}

.option {
  display: block;
  width: 50%;
  border-radius: 4px;
}